'use client';

import { useEffect, useState } from "react";
import { StatsCards } from "@/components/admin/dashboard/StatsCards";
import { RevenueChart } from "@/components/admin/dashboard/RevenueChart";
import { RecentBookings } from "@/components/admin/RecentBookings";
import { Booking } from "@/types/admin";
import { CalendarDateRangePicker } from "@/components/admin/dashboard/CalendarDateRangePicker";
import { Button } from "@/components/ui/button";
import { Download } from "lucide-react";
import { Loader2 } from "lucide-react";

export default function AdminDashboardPage() {
    const [bookings, setBookings] = useState<Booking[]>([]);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        fetch('/api/bookings')
            .then(res => res.json())
            .then(data => {
                setBookings(data || []);
                setIsLoading(false);
            })
            .catch(err => {
                console.error("Failed to fetch bookings", err);
                setIsLoading(false);
            });
    }, []);

    // Calculate stats
    const totalRevenue = bookings.reduce((sum, b) => b.status !== 'CANCELLED' ? sum + (b.totalPrice || 0) : sum, 0);
    const totalBookings = bookings.length;
    // Mock active users for now
    const activeUsers = 124;
    const growth = 12.5;

    if (isLoading) {
        return (
            <div className="flex h-full items-center justify-center p-8">
                <Loader2 className="h-8 w-8 animate-spin text-primary" />
            </div>
        );
    }

    return (
        <div className="space-y-8 animate-in fade-in slide-in-from-bottom-5 duration-500">
            <div className="flex flex-col md:flex-row items-center justify-between gap-4">
                <div>
                    <h2 className="text-3xl font-bold tracking-tight">Dashboard</h2>
                    <p className="text-muted-foreground">
                        Overview of your bookings and performance.
                    </p>
                </div>
                <div className="flex items-center space-x-2">
                    <CalendarDateRangePicker />
                    <Button>
                        <Download className="mr-2 h-4 w-4" />
                        Download Report
                    </Button>
                </div>
            </div>

            <StatsCards
                totalRevenue={totalRevenue}
                totalBookings={totalBookings}
                activeUsers={activeUsers}
                growth={growth}
            />

            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-7">
                <RevenueChart />
                <RecentBookings bookings={bookings} />
            </div>
        </div>
    );
}
